* {
  margin: 0;
  padding: 0;
}

body {
  max-height: 900px;
  overflow-x: hidden;
}

#player {
  height: 100vh;
  width: 100vw;
  background-color: #060a3d;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  overflow-x: hidden;
}

#player .header {
  height: 20vh;
  text-align: center;
  order: 1;
}

#player .header h1 {
  color: #fff;
  padding-top: 20px;
  font-size: 20px;
}

#player .header p {
  color: #868aaf;
  font-size: 12px;
}

#player .header .balls {
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

#player .header .balls span {
  width: 5px;
  height: 5px;
  background: #db3baa4d;
  border-radius: 50%;
  margin: 0 4px;
  display: block;
}

#player .header .balls span.current {
  width: 8px;
  height: 8px;
  background: #db3baa;
}

#player .panels {
  max-width: 100vw;
  order: 2;
  align-items: center;
  transition: transform .3s;
  display: flex;
}

#player .panels.panel1 {
  transform: translateX(0);
}

#player .panels.panel2 {
  transform: translateX(-100vw);
}

#player .panels .panel-effect {
  width: 100vw;
  flex-direction: column;
  justify-content: center;
  display: flex;
}

#player .panels .panel-effect .effect {
  height: 60vh;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

#player .panels .panel-effect .effect > div {
  background-size: contain;
  position: absolute;
}

#player .panels .panel-effect .effect .effect-1 {
  width: 70vw;
  height: 70vw;
  background: url("effect-no-move.9460c30c.svg") no-repeat;
  animation: 20s linear infinite rotate;
}

#player .panels .panel-effect .effect .effect-2 {
  width: 60vw;
  height: 60vw;
  background: url("effect-move1.4a23ad8c.svg") no-repeat;
  animation: 10s linear infinite reverse rotate;
}

#player .panels .panel-effect .effect .effect-3 {
  width: 24vw;
  height: 24vw;
  background: url("effect-move2.8db4fd4d.svg") no-repeat;
  animation: 10s linear infinite rotate;
}

#player .panels .panel-effect .lyrics {
  text-align: center;
}

#player .panels .panel-effect .lyrics p {
  color: #868aaf;
  margin-top: 8px;
  font-size: 13px;
}

#player .panels .panel-effect .lyrics p.active {
  color: #868aaf;
}

#player .panels .panel-effect .buttons {
  justify-content: center;
  margin-top: 28px;
  display: flex;
}

#player .panels .panel-effect .buttons svg {
  width: 24px;
  height: 24px;
  margin: 0 28px;
}

#player .panels .panel-lyrics {
  width: 100vw;
  height: 65vh;
  text-align: center;
  font-size: 15px;
  line-height: 2;
  overflow: scroll;
}

#player .panels .panel-lyrics .container {
  font-size: 15px;
  transition: all .3s;
  transform: translateY(-100px);
}

#player .panels .panel-lyrics .container p {
  color: #fff;
}

#player .panels .panel-lyrics .container p.current {
  color: #db3baa;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.footer {
  height: 30vh;
  order: 3;
}

.footer .bar-area {
  color: #868aaf;
  text-align: center;
  align-items: center;
  margin-top: 28px;
  padding: 0 20px;
  font-size: 10px;
  display: flex;
}

.footer .bar-area .time-start {
  width: 40px;
  order: 1;
}

.footer .bar-area .time-end {
  width: 40px;
  order: 3;
}

.footer .bar-area .bar {
  height: 4px;
  background: #0025f1;
  border-radius: 2px;
  flex: 1;
  order: 2;
}

.footer .bar-area .bar .process {
  width: 0%;
  height: 100%;
  background: #db3baa;
  position: relative;
}

.footer .bar-area .bar .process:after {
  content: "";
  width: 13px;
  height: 13px;
  background: url("progress.5eecbf2f.svg") 0 0 / contain no-repeat;
  border-radius: 50%;
  display: block;
  position: absolute;
  top: -5px;
  right: 0;
}

.footer .actions {
  height: 30vw;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  display: flex;
}

.footer .actions svg {
  height: 28px;
  width: 28px;
}

.footer .actions svg.btn-play-pause {
  width: 50px;
  height: 50px;
}

